:root {
    --primaryColor: hsl(94, 55%, 56%);
    --hoverColor: hsl(93, 75%, 76%);
    --paleGreen: hsl(96, 75%, 89%);
    --whileColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyText: hsl(0, 0%, 75%);
    --bgColor: hsl(113, 11%, 85%);
    --inputColor: hsl(330, 12%, 97%);
    // --inputColor: hsl(154, 14%, 90%);

    // font ang Typography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.5rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: .938rem;
    --smallFontSize: .813rem;
    --smallestFontSize: .75rem;
}


// Pages stylings
.loginPage {
    height: 100vh;
    width: 100%;
    margin: aotu;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 7px 50px rgb(214, 223, 213);

    .container {
        height: 75vh;
        width: 60%;
        margin: auto;
        justify-content: space-between;
        border-radius: 10px;
        background: hsl(0, 0%, 91%);

        // video css
        .videoDiv {
            position: relative;
            display: flex;
            flex-basis: 50%;
            width: 70%;
            height: 100%;
            padding: 0px 1.5rem;
            text-align: center;
            justify-content: center;
            flex-direction: column;
            margin: auto;
            border-radius: 10px;
            overflow: hidden;

            video, .footerDiv{
                position: absolute;
            }
            video {
                height: 100%;
                width: 100%;
                object-fit: cover;
                top: 0;
                bottom: 0;
                right: 0;
                left: 0;
            }

            .textDiv{
                position: relative;

                .title {
                    color:var(--whileColor);
                    font-size: 35px;
                    font-weight: 800;
                }

                p {
                    color: var(--whileColor);
                    padding: 1rem;
                    font-weight: 400;
                }
            }

            .footerDiv {
                bottom: 10px;
                padding: 0px 1rem;
                height: 60px;
                background: rgba(255, 255,255, 0.248);
                left: 10px;
                right: 10px;
                justify-content: space-between;
                backdrop-filter: blur(1px);
                border-radius: 10px;

                .text{
                    color: var(--whileColor);
                }

                .btn {
                    background: var(--whileColor);
                    color: var(--primaryColor);
                    padding: 0.8rem 1.5rem;
                    border-radius: 10px;
                    font-weight: 400;
                    transform: .3s ease;

                    &:hover {
                        background: var(--hoverColor);
                        color: var(--blackColor);
                    }
                }
            }
        }

        .formDiv {
            flex-basis: 50%;
            margin: auto;
            flex-direction: column;
            gap: 1.5rem;
            transform: translateY(-1rem);

            .headerDiv {
                text-align: center;
                padding: 1px 0;

                img {
                    width: 60px;
                    padding-bottom: .5rem;
                }

                h3 {
                    font-size: 25px;
                    color: var(--blackColor);
                    font-weight: 600;
                }
            }

            .form {
                gap: 1rem;
                 
                .inputDiv {
                    color: var(--greyText);
                }

                input {
                    background: none;
                    outline: none;
                    border: none;
                    width: 200px;
                }

                label {
                    color: var(--blackColor);
                    font-weight: 400;
                    font-size: 14px;
                    padding: 0.5rem 0;
                    display: block;

                }

                .input {
                    gap: .5rem;
                    padding: 1rem;
                    background: var(--inputColor);
                    border-radius: 10px;
                }
            }

            .btn {
                padding: 0.8rem;
                background: var(--primaryColor);
                border-radius: 10px;
                color: var(--whileColor);
                align-items: center;
                margin: auto;
                justify-content: center;
                font-size: 16px;
                width: 100%;
                gap: .5rem;
                transition: .3s ease;

                .icon {
                    font-size: 25px;
                    transition: .3s ease;
                }

                &:hover{
                    background: var(--hoverColor);
                    color: var(--blackColor);

                    .icon {
                        color: var(--blackColor);
                        transform: translateX(5px);
                    }
                }
            }

            .forgotPassword, .haveAcc{
                font-size: 13px;
                color: var(--blackColor);
                opacity: .7;
                text-align: center;

                a{
                    text-decoration: underline;
                    cursor: pointer;
                    opacity: 1;
                }
            }
        }
    }
}